body {
	min-width: 320px;
	color: $dark;
	background: #f7f7f8;
	font-family: $familyPrimary;
	@include f(20px, 400, 1.6);
	&.is-overflow-hidden {
		overflow: hidden; }
	&.is-fixed {
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
 } }		// height: 100vh

.out {
	overflow: hidden; }

.inner {
	position: relative;
	margin: 0 auto;
	max-width: 1440px;
	@include clr; }
