.footer {
	padding-bottom: 100px;
	color: $white;
	background: $dark;
	@include sm {
		padding-bottom: 20px; }
	&_sm {
		padding-top: 242px;
		@include sm {
			padding-top: 20px; } }
	&__inner {
		padding: 0 35px;
		margin: 0 auto;
		max-width: 1310px;
		@include clr; }
	&__top {
		padding: 680px 0 162px;
		text-align: center;
		@include md {
			padding: 100px 0; }
		@include sm {
			text-align: left; } }
	&__title {
		margin-bottom: 40px;
		@include sm {
			max-width: 280px;
			@include f(37px, null, 1.1352); } }
	&__label {
		margin-top: 10px;
		opacity: 0.4;
		@include f(15px, 400, 18px);
		letter-spacing: 0.25px;
		@include sm {
			margin-top: 20px;
			opacity: 1; } }
	&__btn {
		padding: 0 33px;
		height: 60px;
		@include xs {
			padding: 0 34px; }
		.btn__title {
			@include f(14px, null, 18px);
			text-transform: uppercase; } }
	&__main {
		@include clr;
		@include sm {
			padding-top: 40px; } }
	&__contacts {
		float: left;
		width: 50%;
		@include sm {
			float: none;
			width: auto; } }
	&__right {
		float: right;
		width: 50%; }
	&__mail {
		position: relative;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 25px;
		color: $red;
		font-family: $familySecondary;
		@include f(28px, 400, 38px);
		text-decoration: none;
		@include sm {
			margin-bottom: 10px;
			@include f(20px, null, 32px); }
		@include hover {
			&:before {
				transform: scaleX(1); } }
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			border-bottom: 2px solid $red;
			transform: scaleX(0);
			transform-origin: left center;
			transition: transform 0.4s; } }
	&__phone {
		position: relative;
		display: inline-block;
		vertical-align: top;
		color: $white;
		font-family: $familySecondary;
		@include f(15px, 400, 18px);
		text-decoration: none;
		@include sm {
			@include f(18px, null, 28px); }
		@include hover {
			&:before {
				transform: scaleX(1); } }
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			border-bottom: 2px solid $white;
			transform: scaleX(0);
			transform-origin: left center;
			transition: transform 0.3s; } }
	&__social {
		padding-top: 10px;
		margin: 0 -10px;
		font-size: 0;
		@include sm {
			padding-top: 14px;
			margin: 0 -16px; }
		&-item {
			display: inline-block;
			vertical-align: top;
			padding: 0 10px;
			height: 40px;
			opacity: 0.5;
			@include vertical;
			transition: opacity 0.3s;
			@include sm {
				padding: 16px;
				height: auto; }
			@include hover {
				opacity: 1; } }
		&-fb {
			font-size: 17px; }
		&-insta {
			font-size: 16px; }
		&-icon {
			display: inline-block;
			vertical-align: middle;
			fill: $white;
			@include sm {
				font-size: 23px; } } }
	&__right {
		float: right;
		width: 50%;
		max-width: 350px;
		@include md {
			max-width: 300px; }
		@include sm {
			float: none;
			padding-top: 35px;
			width: auto;
			max-width: none; } }
	&__nav {
		float: left;
		@include sm {
			display: none; } }
	&__item {
		font-size: 0;
		&:not(:last-child) {
			margin-bottom: 33px; } }
	&__link {
		display: inline-block;
		vertical-align: top;
		color: $white;
		opacity: 0.5;
		font-family: $familySecondary;
		@include f(15px, 400, 18px);
		letter-spacing: 0.025px;
		text-decoration: none;
		transition: opacity 0.3s;
		@include hover {
			opacity: 1; } }
	&__copyright {
		overflow: hidden; }
	&__logo {
		margin-left: auto;
		margin-right: -10px;
		margin-bottom: 44px;
		color: $white;
		text-align: left;
		@include sm {
			display: none; }
		.logo__icon {
			fill: $white; } }
	&__copy {
		margin-left: auto;
		max-width: 120px;
		opacity: 0.5;
		@include f(13px, 400, 24px);
		letter-spacing: 0.5px;
		text-align: right;
		transition: opacity 0.3s;
		@include sm {
			margin: 0;
			max-width: none;
			text-align: left; } }
	&__control {
		display: inline-block;
		vertical-align: top;
		text-align: center; } }
