.btn {
	position: relative;
	display: inline-block;
	vertical-align: top;
	overflow: visible;
	padding: 0 30px;
	height: 60px;
	color: $white;
	background: none;
	@include vertical;
	@include md {
		padding: 0 20px;
		height: 50px; }
	@include hover {
		&:after {
			background: $lightRed;
			// transform: scale(1.12)
			top: -3px;
			left: -3px;
			right: -3px;
			bottom: -3px; } }
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border-radius: 4px;
		background: $red;
		// transition: background 0.3s, transform 0.3s
		@include gpu;
		transition: top 0.3s, left 0.3s, right 0.3s, bottom 0.3s, background 0.3s; }
	&__plus,
	&__title,
	&__check {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle; }
	&__plus {
		margin-right: 15px;
		font-size: 12px;
		@include md {
			margin-right: 10px;
			font-size: 10px; } }
	&__check {
		margin-right: 10px;
		fill: $white;
		font-size: 14px; }
	&__title {
		font-family: $familySecondary;
		@include f(14px, 700, 18px);
		text-transform: uppercase; }

	//modifications
	&_lg {
		height: 60px;
		@include lg {
			padding: 0 20px; }
		@include md {
			padding: 0 25px;
			height: 60px; }
		@include xs {
			padding: 0 35px 0 20px; }
		.btn__title {
			@include md {
				@include f(18px, 500, 23px);
				text-transform: none; } }
		.btn__plus {
			@include md {
				margin-right: 15px;
				font-size: 12px; } } }
	&_xl {
		height: 70px;
		@include md {
			height: 70px; }
		.btn__title {
			@include f(18px, 700, 23px); } }
	&_wide {
		width: 100%; } }
