//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$familyPrimary: 'Proxima Nova';
$familySecondary: 'Sofia Pro';

//PROXIMA NOVA
@font-face {
	@include fontdefault(normal, 300, $familyPrimary);
	@include fontpath($familyPrimary + ' Light', $familyPrimary + '-Light', proximanova-light); }

@font-face {
	@include fontdefault(italic, 300, $familyPrimary);
	@include fontpath($familyPrimary + ' Light Italic', $familyPrimary + '-LightItalic', proximanova-lightitalic); }

@font-face {
	@include fontdefault(normal, 400, $familyPrimary);
	@include fontpath($familyPrimary + ' Regular', $familyPrimary + '-Regular', proximanova-regular); }

@font-face {
	@include fontdefault(italic, 400, $familyPrimary);
	@include fontpath($familyPrimary + ' Italic', $familyPrimary + '-Italic', proximanova-regularitalic); }

@font-face {
	@include fontdefault(normal, 600, $familyPrimary);
	@include fontpath($familyPrimary + ' SemiBold', $familyPrimary + '-SemiBold', proximanova-semibold); }

@font-face {
	@include fontdefault(italic, 600, $familyPrimary);
	@include fontpath($familyPrimary + ' SemiBold Italic', $familyPrimary + '-SemiBoldItalic', proximanova-semibolditalic); }

@font-face {
	@include fontdefault(normal, 700, $familyPrimary);
	@include fontpath($familyPrimary + ' Bold', $familyPrimary + '-Bold', proximanova-bold); }

@font-face {
	@include fontdefault(italic, 700, $familyPrimary);
	@include fontpath($familyPrimary + ' Bold Italic', $familyPrimary + '-BoldItalic', proximanova-bolditalic); }

@font-face {
	@include fontdefault(normal, 800, $familyPrimary);
	@include fontpath($familyPrimary + ' ExtraBold', $familyPrimary + '-ExtraBold', proximanova-extrabold); }
@font-face {
	@include fontdefault(italic, 800, $familyPrimary);
	@include fontpath($familyPrimary + ' ExtraBold Italic', $familyPrimary + '-ExtraBoldItalic', proximanova-extrabolditalic); }

@font-face {
	@include fontdefault(normal, 900, $familyPrimary);
	@include fontpath($familyPrimary + ' ExtraBold', $familyPrimary + '-ExtraBold', proximanova-black); }
@font-face {
	@include fontdefault(italic, 900, $familyPrimary);
	@include fontpath($familyPrimary + ' ExtraBold Italic', $familyPrimary + '-ExtraBoldItalic', proximanova-blackitalic); }

//SOFIA PRO
@font-face {
	@include fontdefault(normal, 400, $familySecondary);
	@include fontpath($familySecondary + ' Regular', $familySecondary + '-Regular', sofiaproregular); }

@font-face {
	@include fontdefault(normal, 500, $familySecondary);
	@include fontpath($familySecondary + ' Medium', $familySecondary + '-Medium', sofiapromedium); }

@font-face {
	@include fontdefault(normal, 700, $familySecondary);
	@include fontpath($familySecondary + ' Bold', $familySecondary + '-Bold', sofiaprobold); }

@font-face {
	@include fontdefault(normal, 900, $familySecondary);
	@include fontpath($familySecondary + ' Black', $familySecondary + '-Black', sofiaproblack); }
