$start: rgba($white,0) 0%;
$end:  #f7f7f8 70%;

.map {
	position: relative;
	z-index: -1;
	float: left;
	margin-top: -300px;
	margin-bottom: -466px;
	width: 100%;
	height: 1722px;
	background: #f7f7f8;
	@include md {
		margin: 0;
		height: 775px; }
	&:before {
		content: '';
		position: absolute;
		z-index: 100;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: -moz-radial-gradient(center, ellipse cover, $start, rgba($white, 0) 40%, $end);
		background: -webkit-radial-gradient(center, ellipse cover, $start, rgba($white, 0) 40%, $end);
		background: -o-radial-gradient(center, ellipse cover, $start, rgba($white, 0) 40%, $end);
		background: -ms-radial-gradient(center, ellipse cover, $start, rgba($white, 0) 40%, $end);
		background: radial-gradient(ellipse at center, $start, rgba($white, 0) 40%, $end);
		// background: -moz-radial-gradient(center, ellipse cover, $start, $end)
		// background: -webkit-radial-gradient(center, ellipse cover, $start, $end)
		// background: -o-radial-gradient(center, ellipse cover, $start, $end)
		// background: -ms-radial-gradient(center, ellipse cover, $start, $end)
		// background: radial-gradient(ellipse at center, $start, $end)
		pointer-events: none;
		@include md {
			background: linear-gradient(to bottom, #f7f7f8 0%, rgba($white, 0) 30%); } } }

.location {
	&__content {
		position: absolute;
		top: 200px;
		left: 0;
		right: 0;
		@include md {
			top: 68px; } }
	&__inner {
		padding: 0 180px;
		@include lg {
			padding: 0 90px; }
		@include md {
			padding: 0 35px; } }
	&__label {
		position: absolute;
		top: 8px;
		left: 90px;
		@include lg {
			left: 45px; }
		@include md {
			position: relative;
			top: auto;
			left: auto; } }
	&__title {
		margin-bottom: 30px;
		max-width: 368px;
		@include sm {
			margin-bottom: 20px;
			max-width: 260px; } }
	&__link {
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding-left: 33px;
		padding-right: 29px;
		color: $dark;
		font-size: 0;
		@include hover {
			.location__link-title:before {
				transform: scaleX(1); } }
		&-icon,
		&-title,
		&-arrow {
			display: inline-block;
			vertical-align: middle; }
		&-title {
			position: relative;
			font-family: $familySecondary;
			// +f(18px, 400, 23px)
			@include f(14px, 700, 18px);
			text-transform: uppercase;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				bottom: -2px;
				border-bottom: 2px solid $dark;
				transform: scaleX(0);
				transform-origin: left center;
				transition: transform 0.4s; } }
		&-icon {
			position: absolute;
			// top: 3px
			top: 0;
			left: 0;
			height: 18px; }
		&-arrow {
			position: absolute;
			// top: 6px
			top: 3px;
			right: 0;
			font-size: 14px; } } }
