.profile {
	&__img {
		float: left;
		border-radius: 50%;
		width: 50px;
		height: 50px;
		background-size: 300%;
		background-position: center 10%; }
	&__content {
		padding: 5px 0;
		margin-left: 65px; }
	&__title {
		margin-bottom: 4px;
		@include f(16px, 700, 19px); }
	&__subtitle {
		@include f(14px, null, 17px);
		letter-spacing: 0.29px; } }
