.form {
	position: relative;
	padding: 96px 120px 80px;
	background: $white;
	box-shadow: 0 8px 16px 0 rgba($black, .05);
	@include lg {
		padding: 96px 90px 80px; }
	@include sm {
		padding: 68px 25px 60px; }
	&.is-valid {
		.form {
			&__submit {
				@include hover {
					&:after {
						background: $lightGreen; } }
				&:after {
					background: $green; }
				&-hidden {
					display: inline-block; }
				&-visible {
					display: none; } }
			&__hint {
				opacity: 0.8;
				visibility: visible; } } }
	&__header {
		position: absolute;
		top: -40px;
		left: 0;
		right: 0;
		padding: 0 120px;
		@include lg {
			padding: 0 90px; }
		@include sm {
			padding: 0 25px; }
		@include xs {
			top: -58px; } }
	&__title {
		@include md {
			max-width: 200px; } }
	&__description {
		clear: both;
		margin-bottom: 47px;
		max-width: 315px;
		opacity: 0.8;
		@include xs {
			padding: 0 10px;
			margin-bottom: 25px;
			max-width: 335px; } }
	&__footer {
		position: relative;
		@include sm {
			margin: 0 auto;
			max-width: 400px; } }
	&__body {
		position: relative;
		margin-bottom: 65px; }
	&__field-group {
		margin: 0 -20px;
		@include clr;
		&:not(:last-child) {
			margin-bottom: 85px;
			@include xs {
				margin-bottom: 20px; } } }
	&__field {
		float: left;
		padding: 15px 20px;
		width: 100%;
		&_50 {
			width: 50%;
			@include xs {
				width: 100%; } } }
	&__submit-hidden {
		display: none; }
	&__hint {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		padding-top: 13px;
		opacity: 0;
		visibility: hidden;
		text-align: center;
		transition: opacity 0.3s, visibility 0.3s; }
	&__figure {
		position: absolute;
		z-index: 10;
		right: 30px;
		bottom: 0;
		width: 1.5em;
		height: 1em;
		font-size: 200px;
		transform: translate3d(100%, -31px, 0);
		// transform: translate3d(100%, 55px, 0)
		@include lg {
			bottom: 20px;
			font-size: 12vw;
 }			// transform: translate3d(100%, 30%, 0)
		@include md {
			display: none; } }
	&__figure-container,
	&__figure-in {
		width: 100%;
		height: 100%; }
	&__figure-container {
		will-change: transform;
		&:before,
		&:after {
			z-index: 1;
			box-shadow: 0 8px 16px 0 rgba($black,0.05); } }
	&__figure-in {
		&:before,
		&:after {
			z-index: 2;
			background: $white; } }
	&__figure-container:before,
	&__figure-container:after,
	&__figure-in:before,
	&__figure-in:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 33.33%; }
	&__figure-in:before,
	&__figure-container:before {
		top: 0;
		left: 0;
		width: 66.34%;
		height: 50%; }
	&__figure-in:after,
	&__figure-container:after {
		right: 0;
		bottom: 0;
		width: 66.34%;
		height: 50%; } }

.help-block {
	display: none; }
