.item {
	padding: 60px 60px 77px;
	background: $white;
	box-shadow: 0 8px 16px 0 rgba($black,.05);
	@include gpu;
	@include lg {
		padding: 40px 30px; }
	@include xs {
		padding: 45px 35px 45px; }
	&__figure {
		margin-bottom: 40px;
		height: 40px;
		@include vertical;
		text-align: left;
		@include xs {
			margin-bottom: 20px; } }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		width: 80px; }
	&__title {
		margin-bottom: 15px;
		@include xs {
			margin-bottom: 5px; } }
	&__description {
		overflow: hidden;
		margin-bottom: 33px;
		max-width: 200px;
		height: 3em * 1.445;
		opacity: 0.5;
		line-height: 1.445;
		@include xs {
			margin-bottom: 40px;
			height: auto; } }
	&__footer {
		position: relative;
		display: inline-block;
		vertical-align: top; }
	&__label {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		padding-top: 10px;
		color: $dark;
		opacity: 0.4;
		@include f(14px, 600, 17px);
		text-align: center; } }
