.pricing {
	position: relative;
	z-index: 10;
	padding: 102px 0 12px;
	@include sm {
		padding-top: 47px; }
	&__inner {
		padding: 0 180px;
		@include lg {
			padding: 0 90px; }
		@include md {
			padding: 0 25px; } }
	&__label {
		position: absolute;
		top: 8px;
		left: 90px;
		@include lg {
			left: 45px; }
		@include md {
			position: relative;
			top: auto;
			left: auto; }
		@include sm {
			padding: 0 10px; } }
	&__items {
		margin: -10px -8px;
		font-size: 0;
		@include sm {
			margin: -10px -25px; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 10px 8px;
		width: 33.33%;
		@include md {
			width: 50%; }
		@include xs {
			padding: 10px 0;
			width: 100%; } } }
