.title-lg {
	font-family: $familySecondary;
	@include f(70px, 900, 1.086);
	font-style: normal;
	@include md {
		@include f(37px, null, 1.1352); } }


.title-md {
	font-family: $familySecondary;
	@include f(44px, 700, 1.4);
	@include md {
		@include f(30px, null, 1.266); } }

.title-sm {
	font-family: $familySecondary;
	@include f(30px, 700, 1.267); }

.text-xl {
	@include f(20px, 400, 1.6); }

.text-lg {
	@include f(18px, 400, 1.5); }

.text-md {
	@include f(16px, 400, 1.5); }

.text-sm {
	@include f(14px, 400, 1.429); }

.sm-label {
	opacity: 0.3;
	font-family: $familySecondary;
	@include f(12px, 700, 14px);
	letter-spacing: 1px;
	text-transform: uppercase; }
