.page {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: auto;
	opacity: 0;
	visibility: hidden;
	background: #f7f7f8;
	transition: opacity 0.3s, visibility 0.3s;
	@include sm {
		top: -50px;
		bottom: -50px;
		padding: 50px 0; }
	&.is-open {
		opacity: 1;
		visibility: visible; }
	&__inner {
		position: relative;
		padding: 200px 240px 220px;
		@include lg {
			padding: 100px 90px; }
		@include md {
			padding: 164px 0 0; } }
	&__container {
		margin: 0 auto;
		max-width: 960px;
		@include lg {
			max-width: 900px; }
		@include md {
			max-width: 720px; } }
	// &__form
	// 	margin-bottom: -80px
	// 	+sm
	//// margin-bottom: 0
	&__back {
		position: absolute;
		top: 40px;
		left: 70px;
		padding: 17px 20px;
		color: $dark;
		font-size: 0;
		@include lg {
			top: 20px;
			left: 20px; }
		@include md {
			top: 17px;
			left: 5px; }
		&-icon {
			fill: currentColor;
			font-size: 16px; } } }
