.btn-link {
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding: 0;
	height: 50px;
	color: $dark;
	background: none;
	@include vertical;
	@include hover {
		.btn-link__title:before {
			transform: scaleX(1); } }
	&_lg {
		height: 60px;
		.btn-link__title {
			@include sm {
				@include f(18px, null, 23px); } } }
	&__icon,
	&__title {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle; }
	&__icon {
		margin-right: 15px; }
	&__arrow-right {
		font-size: 14px; }
	&__user {
		font-size: 16px; }
	&__title {
		position: relative;
		font-family: $familySecondary;
		@include f(14px, 700, 18px);
		text-transform: uppercase;
		@include sm {
			@include f(16px, 500, 19px);
			text-transform: none; }
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: -2px;
			border-bottom: 2px solid $dark;
			transform: scaleX(0);
			transform-origin: left center;
			transition: transform 0.3s; } } }
